import React from "react";
import { ReactComponent as ArrowRightIcon } from "../imgs/external-link.svg";

type NewsData = {
  content: string;
  url: string;
};

type NewsGridProps = {
  news: NewsData[];
};

const NewsWest: React.FC<NewsGridProps> = ({ news }) => {
  return (
    <div className="news-item">
      <h1 className="text-2xl font-bold mb-2 text-center w-full">
        Lokaal nieuws
      </h1>
      <div className="table-container">
        <table>
          <tbody>
            {news.map((data) => (
              <tr key={data.content}>
                <td>
                  {data.content}{" "}
                  <a href={data.url} target="_blank">
                    <ArrowRightIcon className="arrow-right-icon" />
                  </a>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default NewsWest;
