import React from "react";
import Images from "../Images";

type WeatherData = {
  time: string;
  icon: string;
  temperature: number;
};

type WeatherGridProps = {
  weather: WeatherData[];
};

const Weather: React.FC<WeatherGridProps> = ({ weather }) => {
  return (
    <>
      <div className="flex justify-center h-full">
        <div className="grid grid-cols-3 gap-4 w-full h-full items-stretch">
          {weather.map((data) => (
            <div
              key={data.time}
              className="grid-item text-center flex flex-col"
            >
              <div className="font-bold">{data.time}</div>
              <div className="icon-container flex flex-col items-center justify-center">
                <img src={Images[data.icon]} alt="Weather Icon" />
                <div className="font-bold">{data.temperature} °C</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Weather;
